.row {
	color: white;
	margin-left: 20px;
	/* background-color: black; */
}

.row_posters {
	display: flex;
	overflow-y: hidden;
	overflow-x: scroll;
	padding: 20px;
}

.row_posters::-webkit-scrollbar {
	display: none;
}

.row_poster {
	max-height: 100px;
	object-fit: contain;
	margin-right: 10px;
	width: 100%;
	transition: transform 450ms;
}
.row_poster:hover {
	opacity: 1;
	transform: scale(1.08);
}
.row_posterLarger {
	max-height: 250px;
}
.row_posterLarger:hover {
	opacity: 1;
	transform: scale(1.08);
}
